import React, { useContext, useState } from 'react';
import { motion } from 'framer-motion';
import { Link } from 'react-router-dom';
import { Howl } from 'howler';
import ConfiguracionContext from '../context/ConfiguracionContext';

const Inicio = () => {
  const { configuracion } = useContext(ConfiguracionContext);
  const [audioReproducido, setAudioReproducido] = useState(false);
  
  // Audio de bienvenida
  const audioInicio = new Howl({
    src: ['/audio/bienvenida.mp3'],
    volume: 0.7,
    preload: true,
    html5: true,
  });
  
  // Reproducir audio de bienvenida solo una vez al cargar
  const reproducirAudioBienvenida = () => {
    if (!audioReproducido && configuracion.audioActivado) {
      audioInicio.play();
      setAudioReproducido(true);
    }
  };
  
  // Efecto de entrada para la animacin
  const containerVariants = {
    hidden: { opacity: 0 },
    visible: { 
      opacity: 1,
      transition: { 
        duration: configuracion.animacionesReducidas ? 0.1 : 1.5,
        when: "beforeChildren",
        staggerChildren: configuracion.animacionesReducidas ? 0 : 0.3
      }
    }
  };
  
  const itemVariants = {
    hidden: { y: 20, opacity: 0 },
    visible: { 
      y: 0, 
      opacity: 1,
      transition: { duration: configuracion.animacionesReducidas ? 0.1 : 0.8 }
    }
  };

  return (
    <motion.div 
      className="min-h-screen bg-space-dark flex flex-col items-center justify-center p-4"
      variants={containerVariants}
      initial="hidden"
      animate="visible"
      onAnimationComplete={reproducirAudioBienvenida}
    >
      <motion.div 
        className="text-center mb-8"
        variants={itemVariants}
      >
        <h1 className="text-4xl md:text-5xl font-bold text-white mb-4">
          El Sistema Solar
        </h1>
        <div className="w-64 h-64 md:w-80 md:h-80 mx-auto mb-8">
          <img 
            src="/images/sistema_solar.png" 
            alt="" 
            className="w-full h-full object-contain"
            aria-hidden="true"
          />
        </div>
      </motion.div>
      
      <motion.div variants={itemVariants}>
        <Link 
          to="/sistema-solar"
          className="
            bg-blue-600 
            hover:bg-blue-700 
            text-white 
            font-bold 
            py-6 
            px-10 
            rounded-xl
            text-2xl
            shadow-lg
            flex
            items-center
            justify-center
            min-w-[240px]
            min-h-[100px]
            focus:outline-none 
            focus:ring-4 
            focus:ring-blue-300
            transition-all
            duration-300
          "
          aria-label="Comenzar a explorar el Sistema Solar"
        >
          <img 
            src="/images/comenzar_pictograma.png" 
            alt="" 
            className="w-12 h-12 mr-4"
            aria-hidden="true"
          />
          Comenzar
        </Link>
      </motion.div>
    </motion.div>
  );
};

export default Inicio;
