import React, { useContext, useState, useEffect } from 'react';
import { motion } from 'framer-motion';
import { Link } from 'react-router-dom';
import { Howl } from 'howler';
import ConfiguracionContext from '../context/ConfiguracionContext';

// Datos de los planetas para el juego
const planetas = [
  {
    id: 'mercurio',
    nombre: 'Mercurio',
    color: 'bg-mercury',
    imagen: '/images/mercurio.png',
    pictograma: '/images/pictograma_mercurio.png',
  },
  {
    id: 'venus',
    nombre: 'Venus',
    color: 'bg-venus',
    imagen: '/images/venus.png',
    pictograma: '/images/pictograma_venus.png',
  },
  {
    id: 'tierra',
    nombre: 'Tierra',
    color: 'bg-earth',
    imagen: '/images/tierra.png',
    pictograma: '/images/pictograma_tierra.png',
  },
  {
    id: 'marte',
    nombre: 'Marte',
    color: 'bg-mars',
    imagen: '/images/marte.png',
    pictograma: '/images/pictograma_marte.png',
  },
  {
    id: 'jupiter',
    nombre: 'Jpiter',
    color: 'bg-jupiter',
    imagen: '/images/jupiter.png',
    pictograma: '/images/pictograma_jupiter.png',
  },
  {
    id: 'saturno',
    nombre: 'Saturno',
    color: 'bg-saturn',
    imagen: '/images/saturno.png',
    pictograma: '/images/pictograma_saturno.png',
  },
  {
    id: 'urano',
    nombre: 'Urano',
    color: 'bg-uranus',
    imagen: '/images/urano.png',
    pictograma: '/images/pictograma_urano.png',
  },
  {
    id: 'neptuno',
    nombre: 'Neptuno',
    color: 'bg-neptune',
    imagen: '/images/neptuno.png',
    pictograma: '/images/pictograma_neptuno.png',
  },
];

const JuegoInteractivo = () => {
  const { configuracion } = useContext(ConfiguracionContext);
  const [planetasJuego, setPlanetasJuego] = useState([]);
  const [aciertos, setAciertos] = useState(0);
  const [juegoCompletado, setJuegoCompletado] = useState(false);
  const [instruccionMostrada, setInstruccionMostrada] = useState(true);
  
  // Sonidos
  const sonidoAcierto = new Howl({
    src: ['/audio/acierto.mp3'],
    volume: 0.7,
    preload: true,
    html5: true,
  });
  
  const sonidoCompletado = new Howl({
    src: ['/audio/juego_completado.mp3'],
    volume: 0.7,
    preload: true,
    html5: true,
  });
  
  const sonidoInstruccion = new Howl({
    src: ['/audio/instruccion_juego.mp3'],
    volume: 0.7,
    preload: true,
    html5: true,
  });
  
  // Inicializar juego con 3 planetas aleatorios
  useEffect(() => {
    const planetasAleatorios = [...planetas]
      .sort(() => 0.5 - Math.random())
      .slice(0, 3)
      .map(planeta => ({
        ...planeta,
        correcto: false,
        posicionX: 0,
        posicionY: 0,
      }));
    
    setPlanetasJuego(planetasAleatorios);
    
    // Reproducir instruccin
    if (configuracion.audioActivado) {
      setTimeout(() => {
        sonidoInstruccion.play();
      }, 1000);
    }
  }, []);
  
  // Verificar si el juego est completado
  useEffect(() => {
    if (aciertos === planetasJuego.length && planetasJuego.length > 0) {
      setJuegoCompletado(true);
      
      if (configuracion.audioActivado) {
        sonidoCompletado.play();
      }
    }
  }, [aciertos, planetasJuego.length]);
  
  // Manejar arrastre de nombre
  const handleDragEnd = (info, planeta) => {
    // Verificar si el arrastre termin sobre la imagen correcta
    const elementos = document.elementsFromPoint(info.point.x, info.point.y);
    const imagenPlaneta = elementos.find(el => el.dataset && el.dataset.planetaId === planeta.id);
    
    if (imagenPlaneta) {
      // Acierto
      const nuevosPlanetas = planetasJuego.map(p => 
        p.id === planeta.id ? { ...p, correcto: true } : p
      );
      
      setPlanetasJuego(nuevosPlanetas);
      setAciertos(prev => prev + 1);
      
      if (configuracion.audioActivado) {
        sonidoAcierto.play();
      }
    }
  };
  
  // Reiniciar juego
  const reiniciarJuego = () => {
    const planetasAleatorios = [...planetas]
      .sort(() => 0.5 - Math.random())
      .slice(0, 3)
      .map(planeta => ({
        ...planeta,
        correcto: false,
        posicionX: 0,
        posicionY: 0,
      }));
    
    setPlanetasJuego(planetasAleatorios);
    setAciertos(0);
    setJuegoCompletado(false);
    
    if (configuracion.audioActivado) {
      setTimeout(() => {
        sonidoInstruccion.play();
      }, 1000);
    }
  };
  
  // Ocultar instruccin
  const ocultarInstruccion = () => {
    setInstruccionMostrada(false);
  };

  return (
    <motion.div 
      className="min-h-screen bg-space-dark flex flex-col items-center justify-center p-4"
      initial={{ opacity: 0 }}
      animate={{ opacity: 1 }}
      transition={{ duration: configuracion.animacionesReducidas ? 0.1 : 0.8 }}
    >
      {/* Botones de navegacin */}
      <div className="absolute top-4 left-4 z-10">
        <Link 
          to="/sistema-solar"
          className="
            bg-gray-800 
            hover:bg-gray-700 
            text-white 
            font-bold 
            py-3 
            px-5 
            rounded-xl
            flex
            items-center
            justify-center
            focus:outline-none 
            focus:ring-4 
            focus:ring-gray-500
            transition-all
            duration-300
          "
          aria-label="Volver al Sistema Solar"
        >
          <span className="text-2xl"></span>
        </Link>
      </div>
      
      <div className="absolute top-4 right-4 z-10">
        <Link 
          to="/"
          className="
            bg-gray-800 
            hover:bg-gray-700 
            text-white 
            font-bold 
            py-3 
            px-5 
            rounded-xl
            flex
            items-center
            justify-center
            focus:outline-none 
            focus:ring-4 
            focus:ring-gray-500
            transition-all
            duration-300
          "
          aria-label="Volver a inicio"
        >
          <span className="text-2xl"></span>
        </Link>
      </div>
      
      {/* Ttulo */}
      <motion.h1 
        className="text-4xl md:text-5xl font-bold text-white mb-8"
        initial={{ y: -20, opacity: 0 }}
        animate={{ y: 0, opacity: 1 }}
        transition={{ duration: configuracion.animacionesReducidas ? 0.1 : 0.8 }}
      >
        Juego de Planetas
      </motion.h1>
      
      {/* Instruccin animada */}
      {instruccionMostrada && !juegoCompletado && (
        <motion.div 
          className="absolute inset-0 bg-black bg-opacity-80 flex items-center justify-center z-50"
          initial={{ opacity: 1 }}
          animate={{ opacity: 1 }}
          exit={{ opacity: 0 }}
          onClick={ocultarInstruccion}
        >
          <motion.div 
            className="bg-gray-800 rounded-xl p-8 max-w-lg text-center"
            initial={{ scale: 0.8 }}
            animate={{ scale: 1 }}
            transition={{ duration: 0.5 }}
          >
            <h2 className="text-3xl font-bold text-white mb-6">Cmo jugar?</h2>
            
            <div className="flex flex-col items-center mb-6">
              <motion.div
                animate={{
                  x: [0, 150, 150],
                  y: [0, 0, 0],
                  opacity: [1, 1, 0.8],
                  scale: [1, 1.1, 1]
                }}
                transition={{
                  duration: 4,
                  repeat: Infinity,
                  repeatDelay: 1
                }}
                className="bg-blue-600 text-white font-bold py-3 px-6 rounded-xl mb-4"
              >
                Nombre del planeta
              </motion.div>
              
              <motion.div
                className="w-16 h-16 bg-red-500 rounded-full"
                animate={{
                  scale: [1, 1.2, 1],
                  borderRadius: ["50%", "50%", "50%"]
                }}
                transition={{
                  duration: 4,
                  delay: 2,
                  repeat: Infinity,
                  repeatDelay: 1
                }}
              />
            </div>
            
            <p className="text-xl text-white mb-6">
              Arrastra el nombre del planeta hasta su imagen correspondiente
            </p>
            
            <button
              className="
                bg-green-600 
                hover:bg-green-700 
                text-white 
                font-bold 
                py-4 
                px-8 
                rounded-xl
                text-xl
                shadow-lg
                focus:outline-none 
                focus:ring-4 
                focus:ring-green-300
                transition-all
                duration-300
              "
              onClick={ocultarInstruccion}
            >
              Entendido!
            </button>
          </motion.div>
        </motion.div>
      )}
      
      {/* rea de juego */}
      <div className="w-full max-w-4xl flex flex-col items-center">
        {!juegoCompletado ? (
          <>
            {/* Nombres de planetas (origen) */}
            <div className="flex flex-wrap justify-center gap-4 mb-16">
              {planetasJuego.map((planeta) => (
                !planeta.correcto && (
                  <motion.div
                    key={planeta.id}
                    className={`
                      bg-blue-600 
                      text-white 
                      font-bold 
                      py-4 
                      px-6 
                      rounded-xl
                      text-xl
                      shadow-lg
                      flex
                      items-center
                      cursor-grab
                      active:cursor-grabbing
                      z-10
                    `}
                    drag={!planeta.correcto}
                    dragConstraints={{ left: 0, right: 0, top: 0, bottom: 0 }}
                    dragElastic={0.2}
                    onDragEnd={(_, info) => handleDragEnd(info, planeta)}
                    whileHover={{ scale: 1.05 }}
                    whileTap={{ scale: 0.95 }}
                  >
                    <img 
                      src={planeta.pictograma} 
                      alt="" 
                      className="w-8 h-8 mr-3"
                      aria-hidden="true"
                    />
                    {planeta.nombre}
                  </motion.div>
                )
              ))}
            </div>
            
            {/* Imgenes de planetas (destino) */}
            <div className="grid grid-cols-1 md:grid-cols-3 gap-8">
              {planetasJuego.map((planeta) => (
                <motion.div
                  key={planeta.id}
                  className={`
                    flex 
                    flex-col 
                    items-center 
                    p-4 
                    rounded-xl
                    ${planeta.correcto ? 'bg-green-900 bg-opacity-30' : 'bg-gray-800 bg-opacity-30'}
                    transition-all
                    duration-500
                  `}
                  animate={planeta.correcto ? {
                    scale: [1, 1.1, 1],
                    transition: { duration: 0.5 }
                  } : {}}
                >
                  <div 
                    className="w-32 h-32 flex items-center justify-center"
                    data-planeta-id={planeta.id}
                  >
                    <img 
                      src={planeta.imagen} 
                      alt={planeta.nombre} 
                      className="w-full h-full object-contain"
                    />
                  </div>
                  
                  {planeta.correcto && (
                    <motion.div
                      initial={{ opacity: 0, y: 10 }}
                      animate={{ opacity: 1, y: 0 }}
                      className="text-green-400 font-bold mt-2 text-xl"
                    >
                      Correcto!
                    </motion.div>
                  )}
                </motion.div>
              ))}
            </div>
          </>
        ) : (
          // Pantalla de juego completado
          <motion.div 
            className="flex flex-col items-center justify-center"
            initial={{ opacity: 0, scale: 0.8 }}
            animate={{ opacity: 1, scale: 1 }}
            transition={{ duration: 0.8 }}
          >
            <motion.div
              animate={{
                scale: [1, 1.1, 1],
                rotate: [0, 5, 0, -5, 0],
              }}
              transition={{
                duration: 2,
                repeat: Infinity,
                repeatType: "reverse"
              }}
              className="text-6xl mb-6"
            >
              
            </motion.div>
            
            <h2 className="text-3xl font-bold text-white mb-6">
              Felicidades!
            </h2>
            
            <p className="text-xl text-white mb-8">
              Has completado el juego correctamente
            </p>
            
            <div className="flex gap-4">
              <button
                className="
                  bg-green-600 
                  hover:bg-green-700 
                  text-white 
                  font-bold 
                  py-4 
                  px-8 
                  rounded-xl
                  text-xl
                  shadow-lg
                  focus:outline-none 
                  focus:ring-4 
                  focus:ring-green-300
                  transition-all
                  duration-300
                "
                onClick={reiniciarJuego}
              >
                Jugar de nuevo
              </button>
              
              <Link
                to="/sistema-solar"
                className="
                  bg-blue-600 
                  hover:bg-blue-700 
                  text-white 
                  font-bold 
                  py-4 
                  px-8 
                  rounded-xl
                  text-xl
                  shadow-lg
                  focus:outline-none 
                  focus:ring-4 
                  focus:ring-blue-300
                  transition-all
                  duration-300
                "
              >
                Volver al Sistema Solar
              </Link>
            </div>
          </motion.div>
        )}
      </div>
    </motion.div>
  );
};

export default JuegoInteractivo;
