import React, { useContext, useState, useEffect } from 'react';
import { motion } from 'framer-motion';
import { Link, useParams, useNavigate } from 'react-router-dom';
import { Howl } from 'howler';
import ConfiguracionContext from '../context/ConfiguracionContext';

// Datos de los planetas, sol y luna
const cuerposCelestes = {
  mercurio: {
    nombre: 'Mercurio',
    color: 'bg-mercury',
    tamao: 4879, // dimetro en km
    descripcion: 'El planeta ms pequeo y cercano al Sol',
    imagen: '/images/mercurio.png',
    pictograma: '/images/pictograma_mercurio.png',
  },
  venus: {
    nombre: 'Venus',
    color: 'bg-venus',
    tamao: 12104,
    descripcion: 'El planeta ms caliente del Sistema Solar',
    imagen: '/images/venus.png',
    pictograma: '/images/pictograma_venus.png',
  },
  tierra: {
    nombre: 'Tierra',
    color: 'bg-earth',
    tamao: 12756,
    descripcion: 'Nuestro planeta, el nico con agua lquida en la superficie',
    imagen: '/images/tierra.png',
    pictograma: '/images/pictograma_tierra.png',
  },
  marte: {
    nombre: 'Marte',
    color: 'bg-mars',
    tamao: 6792,
    descripcion: 'El planeta rojo',
    imagen: '/images/marte.png',
    pictograma: '/images/pictograma_marte.png',
  },
  jupiter: {
    nombre: 'Jpiter',
    color: 'bg-jupiter',
    tamao: 142984,
    descripcion: 'El planeta ms grande del Sistema Solar',
    imagen: '/images/jupiter.png',
    pictograma: '/images/pictograma_jupiter.png',
  },
  saturno: {
    nombre: 'Saturno',
    color: 'bg-saturn',
    tamao: 120536,
    descripcion: 'El planeta con anillos',
    imagen: '/images/saturno.png',
    pictograma: '/images/pictograma_saturno.png',
  },
  urano: {
    nombre: 'Urano',
    color: 'bg-uranus',
    tamao: 51118,
    descripcion: 'El planeta que gira de lado',
    imagen: '/images/urano.png',
    pictograma: '/images/pictograma_urano.png',
  },
  neptuno: {
    nombre: 'Neptuno',
    color: 'bg-neptune',
    tamao: 49528,
    descripcion: 'El planeta ms ventoso',
    imagen: '/images/neptuno.png',
    pictograma: '/images/pictograma_neptuno.png',
  },
  sol: {
    nombre: 'Sol',
    color: 'bg-sun',
    tamao: 1392700,
    descripcion: 'La estrella del Sistema Solar',
    imagen: '/images/sol.png',
    pictograma: '/images/pictograma_sol.png',
  },
  luna: {
    nombre: 'Luna',
    color: 'bg-gray-300',
    tamao: 3475,
    descripcion: 'El satlite natural de la Tierra',
    imagen: '/images/luna.png',
    pictograma: '/images/pictograma_luna.png',
  },
};

// Orden de los planetas para navegacin
const ordenPlanetas = ['mercurio', 'venus', 'tierra', 'marte', 'jupiter', 'saturno', 'urano', 'neptuno', 'sol', 'luna'];

const DetallePlaneta = () => {
  const { configuracion } = useContext(ConfiguracionContext);
  const { id } = useParams();
  const navigate = useNavigate();
  const [audioReproducido, setAudioReproducido] = useState(false);
  
  // Obtener el cuerpo celeste actual
  const cuerpoCeleste = cuerposCelestes[id];
  
  // Si no existe, redirigir a la vista principal
  useEffect(() => {
    if (!cuerpoCeleste) {
      navigate('/sistema-solar');
    }
  }, [cuerpoCeleste, navigate]);
  
  // Audio de descripcin
  const audio = new Howl({
    src: [`/audio/${id}.mp3`],
    volume: 0.7,
    preload: true,
    html5: true,
  });
  
  // Reproducir audio automticamente al cargar
  useEffect(() => {
    if (!audioReproducido && configuracion.audioActivado && cuerpoCeleste) {
      audio.play();
      setAudioReproducido(true);
    }
  }, [audioReproducido, configuracion.audioActivado, cuerpoCeleste]);
  
  // Obtener ndice actual para navegacin
  const indiceActual = ordenPlanetas.indexOf(id);
  const planetaAnterior = indiceActual > 0 ? ordenPlanetas[indiceActual - 1] : ordenPlanetas[ordenPlanetas.length - 1];
  const planetaSiguiente = indiceActual < ordenPlanetas.length - 1 ? ordenPlanetas[indiceActual + 1] : ordenPlanetas[0];
  
  // Reproducir audio manualmente
  const reproducirAudio = () => {
    if (configuracion.audioActivado) {
      audio.play();
    }
  };
  
  if (!cuerpoCeleste) return null;

  return (
    <motion.div 
      className="min-h-screen bg-space-dark flex flex-col items-center justify-center p-4"
      initial={{ opacity: 0 }}
      animate={{ opacity: 1 }}
      exit={{ opacity: 0 }}
      transition={{ duration: configuracion.animacionesReducidas ? 0.1 : 0.8 }}
    >
      {/* Botones de navegacin */}
      <div className="absolute top-4 left-4 z-10">
        <Link 
          to="/sistema-solar"
          className="
            bg-gray-800 
            hover:bg-gray-700 
            text-white 
            font-bold 
            py-3 
            px-5 
            rounded-xl
            flex
            items-center
            justify-center
            focus:outline-none 
            focus:ring-4 
            focus:ring-gray-500
            transition-all
            duration-300
          "
          aria-label="Volver al Sistema Solar"
        >
          <span className="text-2xl"></span>
        </Link>
      </div>
      
      <div className="absolute top-4 right-4 z-10">
        <Link 
          to="/"
          className="
            bg-gray-800 
            hover:bg-gray-700 
            text-white 
            font-bold 
            py-3 
            px-5 
            rounded-xl
            flex
            items-center
            justify-center
            focus:outline-none 
            focus:ring-4 
            focus:ring-gray-500
            transition-all
            duration-300
          "
          aria-label="Volver a inicio"
        >
          <span className="text-2xl"></span>
        </Link>
      </div>
      
      {/* Navegacin entre planetas */}
      <div className="absolute top-1/2 left-4 transform -translate-y-1/2">
        <Link 
          to={`/planeta/${planetaAnterior}`}
          className="
            bg-gray-800 
            hover:bg-gray-700 
            text-white 
            font-bold 
            py-6 
            px-4 
            rounded-xl
            flex
            items-center
            justify-center
            focus:outline-none 
            focus:ring-4 
            focus:ring-gray-500
            transition-all
            duration-300
          "
          aria-label={`Ver ${cuerposCelestes[planetaAnterior].nombre}`}
        >
          <span className="text-3xl"></span>
        </Link>
      </div>
      
      <div className="absolute top-1/2 right-4 transform -translate-y-1/2">
        <Link 
          to={`/planeta/${planetaSiguiente}`}
          className="
            bg-gray-800 
            hover:bg-gray-700 
            text-white 
            font-bold 
            py-6 
            px-4 
            rounded-xl
            flex
            items-center
            justify-center
            focus:outline-none 
            focus:ring-4 
            focus:ring-gray-500
            transition-all
            duration-300
          "
          aria-label={`Ver ${cuerposCelestes[planetaSiguiente].nombre}`}
        >
          <span className="text-3xl"></span>
        </Link>
      </div>
      
      {/* Contenido principal */}
      <div className="flex flex-col items-center justify-center max-w-4xl w-full">
        {/* Nombre del planeta */}
        <motion.h1 
          className="text-4xl md:text-5xl font-bold text-white mb-8"
          initial={{ y: -20, opacity: 0 }}
          animate={{ y: 0, opacity: 1 }}
          transition={{ duration: configuracion.animacionesReducidas ? 0.1 : 0.8 }}
        >
          {cuerpoCeleste.nombre}
        </motion.h1>
        
        {/* Imagen del planeta */}
        <motion.div 
          className="w-64 h-64 md:w-80 md:h-80 mb-8 relative"
          animate={{ 
            rotate: configuracion.animacionesReducidas ? 0 : 360,
          }}
          transition={{ 
            duration: configuracion.animacionesReducidas ? 0 : 60,
            ease: "linear",
            repeat: Infinity,
          }}
        >
          <img 
            src={cuerpoCeleste.imagen} 
            alt="" 
            className="w-full h-full object-contain"
            aria-hidden="true"
          />
        </motion.div>
        
        {/* Botn de audio */}
        <motion.button
          className="
            bg-blue-600 
            hover:bg-blue-700 
            text-white 
            font-bold 
            py-4 
            px-8 
            rounded-xl
            text-xl
            shadow-lg
            flex
            items-center
            justify-center
            mb-8
            focus:outline-none 
            focus:ring-4 
            focus:ring-blue-300
            transition-all
            duration-300
          "
          onClick={reproducirAudio}
          whileHover={{ scale: 1.05 }}
          whileTap={{ scale: 0.95 }}
          aria-label={`Escuchar: Este es ${cuerpoCeleste.nombre}`}
        >
          <span className="mr-3 text-2xl"></span>
          Escuchar
        </motion.button>
        
        {/* Comparacin de tamao */}
        <motion.div 
          className="bg-gray-800 rounded-xl p-6 w-full max-w-md"
          initial={{ y: 20, opacity: 0 }}
          animate={{ y: 0, opacity: 1 }}
          transition={{ duration: configuracion.animacionesReducidas ? 0.1 : 0.8, delay: 0.3 }}
        >
          <h2 className="text-xl font-bold text-white mb-4 text-center">Comparacin con la Tierra</h2>
          <div className="flex items-end justify-center space-x-8">
            {/* Planeta actual */}
            <div className="flex flex-col items-center">
              <div 
                className={`${cuerpoCeleste.color} rounded-full`}
                style={{
                  width: `${Math.min(100, (cuerpoCeleste.tamao / 12756) * 30)}px`,
                  height: `${Math.min(100, (cuerpoCeleste.tamao / 12756) * 30)}px`,
                  minWidth: '10px',
                  minHeight: '10px',
                }}
              ></div>
              <p className="text-white mt-2">{cuerpoCeleste.nombre}</p>
            </div>
            
            {/* Tierra (referencia) */}
            <div className="flex flex-col items-center">
              <div 
                className="bg-earth rounded-full"
                style={{
                  width: '30px',
                  height: '30px',
                }}
              ></div>
              <p className="text-white mt-2">Tierra</p>
            </div>
          </div>
        </motion.div>
      </div>
    </motion.div>
  );
};

export default DetallePlaneta;
