import React, { useContext, useState, useRef, useEffect } from 'react';
import { motion } from 'framer-motion';
import { Link } from 'react-router-dom';
import { Howl } from 'howler';
import ConfiguracionContext from '../context/ConfiguracionContext';

// Datos de los planetas
const planetas = [
  {
    id: 'mercurio',
    nombre: 'Mercurio',
    color: 'bg-mercury',
    distancia: 120, // Distancia desde el centro en pxeles
    tamao: 20, // Tamao del planeta en pxeles
    velocidad: 88, // Velocidad orbital (menor = ms rpido)
  },
  {
    id: 'venus',
    nombre: 'Venus',
    color: 'bg-venus',
    distancia: 150,
    tamao: 28,
    velocidad: 225,
  },
  {
    id: 'tierra',
    nombre: 'Tierra',
    color: 'bg-earth',
    distancia: 180,
    tamao: 30,
    velocidad: 365,
  },
  {
    id: 'marte',
    nombre: 'Marte',
    color: 'bg-mars',
    distancia: 210,
    tamao: 24,
    velocidad: 687,
  },
  {
    id: 'jupiter',
    nombre: 'Jpiter',
    color: 'bg-jupiter',
    distancia: 260,
    tamao: 60,
    velocidad: 4333,
  },
  {
    id: 'saturno',
    nombre: 'Saturno',
    color: 'bg-saturn',
    distancia: 320,
    tamao: 50,
    velocidad: 10759,
  },
  {
    id: 'urano',
    nombre: 'Urano',
    color: 'bg-uranus',
    distancia: 370,
    tamao: 40,
    velocidad: 30687,
  },
  {
    id: 'neptuno',
    nombre: 'Neptuno',
    color: 'bg-neptune',
    distancia: 420,
    tamao: 38,
    velocidad: 60190,
  },
];

const SistemaSolar = () => {
  const { configuracion } = useContext(ConfiguracionContext);
  const [planetaSeleccionado, setPlanetaSeleccionado] = useState(null);
  const [audioReproducido, setAudioReproducido] = useState(false);
  const sistemaSolarRef = useRef(null);
  
  // Audio de introduccin
  const audioIntro = new Howl({
    src: ['/audio/sistema_solar.mp3'],
    volume: 0.7,
    preload: true,
    html5: true,
  });
  
  // Audios para cada planeta
  const audiosPlanetas = {};
  planetas.forEach(planeta => {
    audiosPlanetas[planeta.id] = new Howl({
      src: [`/audio/${planeta.id}.mp3`],
      volume: 0.7,
      preload: true,
      html5: true,
    });
  });
  
  // Reproducir audio de introduccin solo una vez al cargar
  useEffect(() => {
    if (!audioReproducido && configuracion.audioActivado) {
      audioIntro.play();
      setAudioReproducido(true);
    }
  }, [audioReproducido, configuracion.audioActivado]);
  
  // Manejar clic en planeta
  const handlePlanetaClick = (planeta) => {
    setPlanetaSeleccionado(planeta);
    
    if (configuracion.audioActivado) {
      audiosPlanetas[planeta.id].play();
    }
  };
  
  // Calcular velocidad de animacin basada en preferencias
  const getVelocidad = (velocidadBase) => {
    // Convertir a segundos para la animacin CSS
    let velocidad = velocidadBase / 10;
    
    // Reducir velocidad si las animaciones estn reducidas
    if (configuracion.animacionesReducidas) {
      velocidad = Math.max(velocidad, 60); // Mnimo 60s para animaciones reducidas
    }
    
    return `${velocidad}s`;
  };

  return (
    <motion.div 
      className="min-h-screen bg-space-dark flex flex-col items-center justify-center relative overflow-hidden"
      initial={{ opacity: 0 }}
      animate={{ opacity: 1 }}
      transition={{ duration: configuracion.animacionesReducidas ? 0.1 : 1 }}
    >
      {/* Botones de navegacin */}
      <div className="absolute top-4 left-4 z-10">
        <Link 
          to="/"
          className="
            bg-gray-800 
            hover:bg-gray-700 
            text-white 
            font-bold 
            py-3 
            px-5 
            rounded-xl
            flex
            items-center
            justify-center
            focus:outline-none 
            focus:ring-4 
            focus:ring-gray-500
            transition-all
            duration-300
          "
          aria-label="Volver a inicio"
        >
          <span className="text-2xl"></span>
        </Link>
      </div>
      
      <div className="absolute top-4 right-4 z-10">
        <Link 
          to="/juego"
          className="
            bg-green-600 
            hover:bg-green-700 
            text-white 
            font-bold 
            py-3 
            px-5 
            rounded-xl
            flex
            items-center
            justify-center
            focus:outline-none 
            focus:ring-4 
            focus:ring-green-500
            transition-all
            duration-300
          "
          aria-label="Ir al juego interactivo"
        >
          <span className="text-xl">Juego</span>
        </Link>
      </div>
      
      {/* Sistema Solar */}
      <div 
        ref={sistemaSolarRef}
        className="relative w-[900px] h-[900px] flex items-center justify-center"
        aria-label="Sistema Solar interactivo"
      >
        {/* Sol */}
        <motion.div 
          className="absolute w-80 h-80 bg-sun rounded-full z-10 flex items-center justify-center"
          animate={{ 
            scale: configuracion.animacionesReducidas ? 1 : [1, 1.05, 1],
          }}
          transition={{ 
            duration: 4,
            repeat: Infinity,
            repeatType: "reverse",
          }}
          onClick={() => {
            if (configuracion.audioActivado) {
              new Howl({
                src: ['/audio/sol.mp3'],
                volume: 0.7,
                html5: true,
              }).play();
            }
          }}
          tabIndex={0}
          role="button"
          aria-label="Sol"
        >
          <Link 
            to="/planeta/sol"
            className="w-full h-full rounded-full flex items-center justify-center text-3xl font-bold"
          >
            Sol
          </Link>
        </motion.div>
        
        {/* rbitas y planetas */}
        {planetas.map((planeta) => (
          <React.Fragment key={planeta.id}>
            {/* rbita */}
            <div 
              className="orbit absolute rounded-full border border-gray-700"
              style={{
                width: planeta.distancia * 2,
                height: planeta.distancia * 2,
              }}
            />
            
            {/* Planeta */}
            <motion.div
              className={`planet ${planeta.color} rounded-full flex items-center justify-center`}
              style={{
                width: planeta.tamao,
                height: planeta.tamao,
                left: `calc(50% - ${planeta.tamao / 2}px)`,
                animationDuration: getVelocidad(planeta.velocidad),
              }}
              animate={{
                rotate: configuracion.animacionesReducidas ? 0 : 360,
              }}
              transition={{
                duration: configuracion.animacionesReducidas ? 0 : planeta.velocidad / 10,
                ease: "linear",
                repeat: Infinity,
              }}
              custom={planeta.distancia}
              onClick={() => handlePlanetaClick(planeta)}
              tabIndex={0}
              role="button"
              aria-label={planeta.nombre}
              whileHover={{ scale: 1.2 }}
              whileTap={{ scale: 0.95 }}
            >
              <Link 
                to={`/planeta/${planeta.id}`}
                className="w-full h-full rounded-full"
                aria-label={`Ver detalles de ${planeta.nombre}`}
              />
            </motion.div>
          </React.Fragment>
        ))}
        
        {/* Luna (como satlite de la Tierra) */}
        <motion.div
          className="absolute bg-gray-300 rounded-full"
          style={{
            width: 10,
            height: 10,
            left: `calc(50% - 5px)`,
            top: `calc(50% - 5px)`,
          }}
          animate={{
            rotate: configuracion.animacionesReducidas ? 0 : 360,
          }}
          transition={{
            duration: configuracion.animacionesReducidas ? 0 : 27,
            ease: "linear",
            repeat: Infinity,
          }}
          tabIndex={0}
          role="button"
          aria-label="Luna"
          whileHover={{ scale: 1.2 }}
          whileTap={{ scale: 0.95 }}
        >
          <Link 
            to="/planeta/luna"
            className="w-full h-full rounded-full"
            aria-label="Ver detalles de la Luna"
          />
        </motion.div>
      </div>
      
      {/* Instruccin */}
      <motion.div 
        className="absolute bottom-8 left-0 right-0 text-center text-white text-xl"
        initial={{ opacity: 0, y: 20 }}
        animate={{ opacity: 1, y: 0 }}
        transition={{ delay: 1, duration: 0.8 }}
      >
        Toca un planeta para explorarlo
      </motion.div>
    </motion.div>
  );
};

export default SistemaSolar;
