import React, { useState, useEffect } from 'react';
import { BrowserRouter as Router, Routes, Route } from 'react-router-dom';
import { motion, AnimatePresence } from 'framer-motion';
import { Howl } from 'howler';
import SistemaSolar from './screens/SistemaSolar';
import Inicio from './screens/Inicio';
import DetallePlaneta from './screens/DetallePlaneta';
import JuegoInteractivo from './screens/JuegoInteractivo';
import ConfiguracionContext from './context/ConfiguracionContext';

function App() {
  const [configuracion, setConfiguracion] = useState({
    audioActivado: true,
    animacionesReducidas: false,
    altoContraste: false,
    tamaoElementos: 'normal', // 'normal', 'grande', 'muy-grande'
  });

  // Detectar preferencias de usuario para movimiento reducido
  useEffect(() => {
    const mediaQuery = window.matchMedia('(prefers-reduced-motion: reduce)');
    if (mediaQuery.matches) {
      setConfiguracion(prev => ({...prev, animacionesReducidas: true}));
    }
  }, []);

  return (
    <ConfiguracionContext.Provider value={{ configuracion, setConfiguracion }}>
      <Router>
        <AnimatePresence mode="wait">
          <Routes>
            <Route path="/" element={<Inicio />} />
            <Route path="/sistema-solar" element={<SistemaSolar />} />
            <Route path="/planeta/:id" element={<DetallePlaneta />} />
            <Route path="/juego" element={<JuegoInteractivo />} />
          </Routes>
        </AnimatePresence>
      </Router>
    </ConfiguracionContext.Provider>
  );
}

export default App;
